import Image from 'next/image';
import Link from 'next/link';
import { Button } from '@/components/ui/button';
import { Card, CardContent } from '@/components/ui/card';
import { PlaceHolderImages } from '@/lib/placeholder-images';
import { products, productTypes } from '@/lib/data';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"

export default function Home() {
  const heroImage = PlaceHolderImages.find((img) => img.id === 'hero-1');
  const allProducts = products.map((product) => {
    const image = PlaceHolderImages.find((img) => img.id === product.imageId);
    return { ...product, imageUrl: image?.imageUrl, imageHint: image?.imageHint };
  });

  return (
    <div className="flex flex-col min-h-dvh">
      <main className="flex-1">
        <section className="w-full bg-gradient-to-r from-teal-50 to-cyan-100 dark:from-teal-900/50 dark:to-cyan-900/50">
          <div className="container mx-auto px-4">
            <div className="grid md:grid-cols-2 gap-8 items-center">
              <div className="py-12 md:py-24 text-center md:text-left">
                <h1 className="text-4xl md:text-5xl lg:text-6xl font-headline font-bold text-foreground text-balance">
                  The taste of your celebration
                </h1>
                <p className="mt-4 max-w-lg mx-auto md:mx-0 text-lg text-muted-foreground">
                  Individual and custom made cakes. Guests of your holiday will be delighted.
                </p>
                <div className="mt-8 flex gap-4 justify-center md:justify-start">
                  <Button size="lg" className="bg-white text-primary-foreground hover:bg-gray-200 text-black">
                    <Link href="/custom-order">Buy</Link>
                  </Button>
                  <Button size="lg" variant="outline" className="bg-transparent border-black text-black dark:border-white dark:text-white">
                    <Link href="/products">View all</Link>
                  </Button>
                </div>
              </div>
              <div className="relative h-64 md:h-auto md:aspect-[4/3] -mb-16 md:mb-0">
                {heroImage && (
                  <Image
                    src={heroImage.imageUrl}
                    alt={heroImage.description}
                    fill
                    className="object-contain"
                    priority
                    data-ai-hint={heroImage.imageHint}
                  />
                )}
              </div>
            </div>
          </div>
        </section>

        <section id="products" className="py-16 md:py-24 bg-background">
          <div className="container mx-auto px-4">
          <Tabs defaultValue="Cakes" className="w-full">
            <TabsList className="grid w-full grid-cols-2 md:grid-cols-5 mb-8 bg-transparent p-0">
              {productTypes.map(category => (
                <TabsTrigger key={category} value={category} className="data-[state=active]:bg-primary data-[state=active]:text-primary-foreground rounded-full data-[state=active]:shadow-lg">{category}</TabsTrigger>
              ))}
            </TabsList>

            {productTypes.map(category => (
              <TabsContent key={category} value={category}>
                <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4 md:gap-8">
                  {allProducts.filter(p => p.category === category || (category === "Cakes" && (p.category === "Cupcakes" || p.category === "Other"))).map((product) => (
                    <Card key={product.id} className="overflow-hidden border-none shadow-lg group">
                      <CardContent className="p-0">
                        {product.imageUrl && (
                          <div className="relative aspect-square">
                            <Image
                              src={product.imageUrl}
                              alt={product.name}
                              fill
                              className="object-cover transition-transform duration-300 group-hover:scale-105"
                              data-ai-hint={product.imageHint}
                            />
                          </div>
                        )}
                        <div className="p-4">
                           <div className="flex justify-between items-center">
                            <div>
                                <h3 className="font-semibold text-lg">{product.name}</h3>
                                <p className="text-muted-foreground">{product.price}</p>
                            </div>
                            <Button size="sm" asChild className="bg-gray-100 text-gray-800 hover:bg-gray-200 dark:bg-gray-800 dark:text-gray-200 dark:hover:bg-gray-700">
                                <Link href="/custom-order">Buy</Link>
                            </Button>
                           </div>
                        </div>
                      </CardContent>
                    </Card>
                  ))}
                  {category === "Cakes" && (
                     <Card className="overflow-hidden border-none shadow-lg group bg-primary text-primary-foreground flex flex-col items-center justify-center p-8 text-center">
                        <div className="text-4xl font-bold">10%</div>
                        <p className="mt-2">Tasty discount when buying from 200$</p>
                        <Button size="sm" asChild className="mt-4 bg-white text-primary hover:bg-gray-200">
                            <Link href="/products">More details</Link>
                        </Button>
                     </Card>
                  )}
                </div>
              </TabsContent>
            ))}
            </Tabs>
          </div>
        </section>
      </main>
    </div>
  );
}
